main {
  min-height: 80vh;
}

h3 {
  padding: 1rem 0;
}

.rating span {
  margin: 0.1rem;
}

h1 {
  font-size: 1.8rem;
  padding: 1rem 0;
}

h2 {
  font-size: 1.4rem;
  padding: 0.5rem 0;
}

.carousel-container {
  width: 100%;
  max-height: 400px;
  overflow: hidden;
  border-radius: 20px;
}


.carousel-item.active {
  display: flex;
  transform: scale(0.98);
  opacity: 0.9;
  
}

.carousel-caption {
  position: absolute;
  top: 10%;
  color: white;
}

.carousel-caption h4 {
  color: #fff;
}

.carousel img {
  display: block;
  width: 100%;
  height: 400px; 
  object-fit: cover; 
  border-radius: 0; 
  padding: 0; 
  margin: 0;
}

.carousel a {
  margin: 0 auto;
}

.carousel-banner {
  width: 100%;
  height: 400px; 
  object-fit: cover;  
  border-radius: 0;
}

.carousel-product-image {
  width: 100%;
  height: 400px;  
  object-fit: contain;
  transform: scale(0.20);
  transition: transform 0.1s ease-in-out;
}

.carousel .carousel-item {
  transition: transform 1s ease-in-out, opacity 1s ease-in-out;
}


@keyframes pulseHover {
  0% {
    transform: scale(1);
  }
  50% {
    transform: scale(1.02);
  }
  100% {
    transform: scale(1);
  }
}

.carousel-container:hover {
  animation: pulseHover 1.0s ease-in-out;
}



@media (max-width: 1920px) {
  .carousel-caption h2 {
    font-size: 2.5vw;
    color: white;
  }
}

.product-card {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-between;
  width: 250px; 
  height: 400px; 
  padding: 15px;
  border: 1px solid #ddd;
  border-radius: 10px;
  box-shadow: 2px 2px 10px rgba(0, 0, 0, 0.1);
  background-color: white;
  text-align: center;
  overflow: hidden;  
}

.product-card img {
  width: 100%;
  height: 180px; 
  object-fit: contain; 
}

.product-card h3 {
  font-size: 1rem;
  margin: 10px 0;
}

.product-card .rating {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 5px;
}

.product-card .price {
  font-size: 1.2rem;
  font-weight: bold;
  color: #000;
}

@keyframes pulse {
  0% {
    transform: scale(1);
    box-shadow: 0 0 5px rgba(0, 0, 0, 0.2);
  }
  50% {
    transform: scale(1.05);
    box-shadow: 0 0 15px rgba(0, 140, 255, 0.6);
  }
  100% {
    transform: scale(1);
    box-shadow: 0 0 5px rgba(0, 0, 0, 0.2);
  }
}

.product-card {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-between;
  width: 250px; 
  height: 400px; 
  padding: 15px;
  border: 1px solid #ddd;
  border-radius: 10px;
  box-shadow: 2px 2px 10px rgba(0, 0, 0, 0.1);
  background-color: white;
  text-align: center;
  overflow: hidden;
  transition: transform 0.3s ease-in-out;
}

.product-card:hover {
  animation: pulse 1s infinite;
  border: 2px solid #008cff; 
}

@import url('https://fonts.googleapis.com/css2?family=Arvo:wght@400;700&display=swap');

.custom-title {
  font-family: 'Arvo', serif;  
  font-weight: 700;  
  font-size: 28px; 
  color: #000; 
  text-transform: uppercase; 
  margin: 20px 0; 
}

